    -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#e5e5e5));
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),
    linear-gradient(to bottom, #ffffff 0%, #e5e5e5 100%);
  background-position: right 0.4em top 50%, 0 0;
  background-repeat: no-repeat, repeat;
  background-size: 0.55em auto, 100%;
  border: 1px solid #aaa;
  border-radius: 0;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  box-sizing: border-box;
  color: #444;
  display: block;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  margin: 1rem 0;
  max-width: 100%;
  padding: 0.6em 1.4em 0.5em 0.8em;
  position: relative;
  width: 100%;
}
.select-component::-ms-expand {
  display: none;
}
.select-component:hover {
  border-color: #888;
}
.select-component:focus {
  border-color: #aaa;
  box-shadow: 0 0 0.5px 1px rgba(59, 153, 252, 0.7);
  box-shadow: 0 0 0 0.5px -moz-mac-focusring;
  color: #222;
  outline: none;
}
.select-component option {
  font-weight: normal;
}
@media (min-width: 30rem) {
  .select-component {
    width: 30%;
  }
}
.spinner {
  align-items: center;
  background-color: transparent;
  -webkit-box-align: center;
  -webkit-box-pack: center;
  display: flex;
  justify-content: center;
  margin: auto;
}
.pulse-container {
  align-items: center;
  -webkit-box-align: center;
  -webkit-box-pack: justify;
  display: flex;
  justify-content: space-between;
  width: 2.5rem;
}
.pulse-bubble {
  background-color: #828282;
  background-color: #828282;
  background-color: var(--color-grey);
  border-radius: 50%;
  height: 0.5rem;
  width: 0.5rem;
}
.pulse-bubble-1 {
  -webkit-animation: pulse 0.4s ease 0s infinite alternate;
  animation: pulse 0.4s ease 0s infinite alternate;
}
.pulse-bubble-2 {
  -webkit-animation: pulse 0.4s ease 0.2s infinite alternate;
  animation: pulse 0.4s ease 0.2s infinite alternate;
}
.pulse-bubble-3 {
  -webkit-animation: pulse 0.4s ease 0.4s infinite alternate;
  animation: pulse 0.4s ease 0.4s infinite alternate;
}
@-webkit-keyframes pulse {
  from {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  to {
    opacity: 0.25;
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
  }
}
@keyframes pulse {
  from {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  to {
    opacity: 0.25;
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
  }
}
header {
  align-items: center;
  border-bottom: 1px solid #ccc;
  -webkit-box-align: center;
  -webkit-box-pack: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0;
  padding: 0.8rem 0 0.5rem;
  position: relative;
  text-align: center;
}
header .logo-wrapper {
  background: url(/kairos/static/media/bbc-logo.96095ff2.svg) 0 0 no-repeat;
  background-size: contain;
  height: 30px;
  width: 100px;
}
footer {
  padding: 2rem 0;
  text-align: center;
}
.footer__inner-block {
  align-content: center;
  bottom: 1rem;
  -webkit-box-pack: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  text-align: center;
  width: 100%;
}
.weather-infos__wrapper {
  margin-top: 10px;
  width: 100%;
}
.weather-widget__wrapper {
  align-items: center;
  align-self: center;
  border: 1px solid #aaa;
  border-radius: 0.3rem;
  -webkit-box-align: center;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  color: #fff;
  display: flex;
  flex-direction: row;
  min-height: 10rem;
  min-height: 250px;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  width: 100%;
}
.weather-widget__wrapper article {
  width: 100%;
}
.weather-widget__wrapper .block-content {
  padding: 10px;
}
.weather-widget__wrapper .icon__wrapper {
  height: 4rem;
  width: 4rem;
}
.weather-widget__wrapper.paris {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0.5))),
    url(/kairos/static/media/paris.d9179f8b.jpg) 0 0 no-repeat;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5)),
    url(/kairos/static/media/paris.d9179f8b.jpg) 0 0 no-repeat;
  background-size: cover;
}
.weather-widget__wrapper.new-york {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0.8)), to(rgba(0, 0, 0, 0.3))),
    url(/kairos/static/media/new-york.bc11cdfd.jpg) 0 0 no-repeat;
  background: linear-gradient(to bottom, rgba(0, 0, 0.8), rgba(0, 0, 0, 0.3)),
    url(/kairos/static/media/new-york.bc11cdfd.jpg) 0 0 no-repeat;
  background-size: cover;
}
.weather-widget__wrapper.london {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0.4))),
    url(/kairos/static/media/london.a3c419e5.jpg) 0 0 no-repeat;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.4)),
    url(/kairos/static/media/london.a3c419e5.jpg) 0 0 no-repeat;
  background-size: cover;
}
.weather-widget__wrapper.singapore {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0.4))),
    url(/kairos/static/media/singapore.61b4c4f7.jpg) 0 0 no-repeat;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.4)),
    url(/kairos/static/media/singapore.61b4c4f7.jpg) 0 0 no-repeat;
  background-size: cover;
}
.weather-widget__wrapper.sydney {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0.3))),
    url(/kairos/static/media/sydney.005f2b2a.jpg) 0 0 no-repeat;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.3)),
    url(/kairos/static/media/sydney.005f2b2a.jpg) 0 0 no-repeat;
  background-size: cover;
}
.weather-widget__wrapper h3 {
  font-size: 1.55rem;
  margin: 0 0 1rem;
  text-transform: capitalize;
}
.weather-widget__wrapper p {
  letter-spacing: 1px;
  margin: 0.5rem auto 0;
  padding: 0;
}
.weather-widget__wrapper p span {
  font-size: 11px;

  text-transform: capitalize;
}
.weather-widget__wrapper p.temperature {
  display: flex;
  font-size: 5.5rem;
  line-height: 5rem;
}
.weather-widget__wrapper p.temperature sup {
  font-size: 1rem;
  line-height: 3rem;
}
